import React, { useRef, useState } from 'react';
import '../style/style.css';
import {
  signup, useAuth, signin, logout,
} from './firebase';

function Login() {
  const [loading, setLoading] = useState(false);
  const currentUser = useAuth();
  const emailRef = useRef();
  const emailRef2 = useRef();
  const passwordRef = useRef();
  const passwordRef2 = useRef();

  const handleSignup = async () => {
    setLoading(true);
    try {
      await signup(emailRef2.current.value, passwordRef2.current.value);
    } catch (e) {
      console.log(e);
      alert('');
    }
    setLoading(false);
  };

  const handleLogin = async () => {
    setLoading(true);
    try {
      await signin(emailRef.current.value, passwordRef.current.value);
    } catch (e) {
      alert('');
      console.log(e);
    }
    setLoading(false);
  };

  const handleLogout = async () => {
    setLoading(true);
    try {
      await logout();
    } catch (e) {
      alert('');
      console.log(e);
    }
    setLoading(false);
  };

  return (
    <div className="loginAndSignUP">

      <div className="loginDiv">
        <p style={{ fontSize: '30px' }}></p>

        :
        <br />
        <input type="text" ref={emailRef} className="loginInput" placeholder="Email" />
        <br />
        :
        <br />
        <input type="password" ref={passwordRef} className="loginInput" placeholder="Password" />
        <br />
        <button type="button" onClick={handleLogin} disabled={loading || currentUser !== null}></button>
        <button type="button" onClick={handleLogout} disabled={loading || currentUser == null}></button>

        <p>
          :
          {currentUser?.email}
        </p>
      </div>

      <div className="loginDiv">
        <p style={{ fontSize: '30px' }}></p>

        :
        <br />
        <input type="text" ref={emailRef2} className="loginInput" placeholder="Email" />
        <br />
        :
        <br />
        <input type="password" ref={passwordRef2} className="loginInput" placeholder="Password" />
        <br />

        <button type="button" onClick={handleSignup} disabled={loading || currentUser !== null}></button>
        <p>
          :
          {currentUser?.email}
        </p>
      </div>

    </div>

  );
}

export default Login;
